import * as React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Checkbox from '@mui/material/Checkbox';
import IconButton from '@mui/material/IconButton';
import CommentIcon from '@mui/icons-material/Comment';

export default function CheckboxList({allCommands, usedCommands, setUsedCommands}) {
  

  const handleToggle = (value) => () => {
    const currentIndex = usedCommands.indexOf(value);
    const newChecked = [...usedCommands];

    if (currentIndex === -1) {
      newChecked.push(value);
    } else {
      newChecked.splice(currentIndex, 1);
    }

    setUsedCommands(newChecked);
  };


  return (
    <>
      {allCommands.map((value) => {
        const labelId = `checkbox-list-label-${value}`;

        return (
          <ListItem
            key={value}
            disablePadding
            dense
          >
            <ListItemButton role={undefined} onClick={handleToggle(value)} dense >
              <ListItemIcon style={{'min-width': '42px'}}>
                <Checkbox
                  edge="start"
                  checked={usedCommands.indexOf(value) !== -1}
                  tabIndex={-1}
                  disableRipple
                  inputProps={{ 'aria-labelledby': labelId }}
                />
              </ListItemIcon>
              <ListItemText id={labelId} primary={value} />
            </ListItemButton>
          </ListItem>
        );
      })}
    </>
  );
}
