import * as React from 'react';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import FilterListIcon from '@mui/icons-material/FilterList';
import IconButton from '@mui/material/IconButton';
import AddIcon from '@mui/icons-material/Add';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import InputLabel from '@mui/material/InputLabel';
import TextField from '@mui/material/TextField';
import OutlinedInput from '@mui/material/OutlinedInput';
import FormControl from '@mui/material/FormControl';
import Divider from '@mui/material/Divider';
import Select from '@mui/material/Select';

export default function Add({allCommands, setEditing, createNew, setCreateNew, collection, setCollection, setIsEditing, setCurrentEditing, setTempCurrentEditing}) {
  const [open, setOpen] = React.useState(false);
  const [newName, setNewName] = React.useState('');
  

  const handleChange = (event) => {
    setCollection(event.target.value);
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
    }
  };

  const handleProceed = (event, reason) => {
    if (reason !== 'backdropClick') {
      setOpen(false);
      setIsEditing(true)
      setTempCurrentEditing(`${collection === "Add New" ? createNew : collection}|${newName}`)
      setCurrentEditing(newName)
      setEditing('')
    }
  };

  return (
    <div>
      <IconButton color="inherit" onClick={handleClickOpen}>
            <AddIcon />
          </IconButton>
        <Dialog disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>Create command</DialogTitle>
        <DialogContent>
          <Box component="form" sx={{ display: 'flex', flexWrap: 'wrap', width: 200 }}>
          <FormControl sx={{mt: 1, minWidth: 200, overflow: 'visible' }} >
            <TextField inputProps={{autocomplete: "off"}} id="outlined-basic" label="Name" variant="outlined" value={newName} onChange={(e) => setNewName(e.target.value)} />
            </FormControl>
            <FormControl sx={{ mt: 2, minWidth: 200, overflow: 'visible' }}>
              <InputLabel id="demo-dialog-select-label">Collection</InputLabel>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={collection}
                onChange={handleChange}
                input={<OutlinedInput label="Collection" />}
              >
                
                {
                  allCommands.map(a => <MenuItem value={a}>{a}</MenuItem>)
                }
                <Divider />
                <MenuItem value="Add New">
                  New
                </MenuItem>
              </Select>
            </FormControl>
            {collection === 'Add New' && <FormControl sx={{mt: 2, minWidth: 200, overflow: 'visible' }} >
              <TextField inputProps={{autocomplete: "off"}} id="outlined-basic" label="Collection Name" variant="outlined" value={createNew} onChange={(e) => setCreateNew(e.target.value)}/>
            </FormControl>}
            
            
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>
          <Button onClick={handleProceed}>Ok</Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
