
import { createTheme, ThemeProvider } from '@mui/material/styles';
import {useEffect, useState} from 'react';
import AppBar from '@mui/material/AppBar';

import Toolbar from '@mui/material/Toolbar';

import Fab from '@mui/material/Fab';

import IconButton from '@mui/material/IconButton';
import Filter from './Filter'
import MenuIcon from '@mui/icons-material/Menu';
import { indigo, pink } from '@mui/material/colors';
import { styled } from '@mui/material/styles';
import MoreIcon from '@mui/icons-material/MoreVert';
import AddIcon from '@mui/icons-material/Add';
import Add from './Add'
import Box from '@mui/material/Box';
import FilterList from '@mui/icons-material/FilterList';
import PlayGround from './Playground'
const data = require('./commands.json');

const StyledFab = styled(Fab)({
  position: 'absolute',
  zIndex: 1,
  top: -30,
  left: 0,
  right: 0,
  margin: '0 auto',
});

const outerTheme = createTheme({
  palette: {
    primary: {
      main: indigo[500],
    },
    secondary: {
      main: pink[500],
    },
  },
});



export default function App() {
  const [commands, setCommands] = useState(data)
  

  
  var allCommands = commands.map(a => a.name)
  const [usedCommands, setUsedCommands] = useState(['Presets'])
  const [isEditing, setIsEditing] = useState(false)
  const [collection, setCollection] = useState('');
  const [createNew, setCreateNew] = useState('');
  const [tempCurrentEditing, setTempCurrentEditing] = useState([])
    const [currentEditing, setCurrentEditing] = useState(null);
    const [input, setInput] = useState('')
    function getSearchParameters() {
      var prmstr = window.location.search.substr(1);
      return prmstr != null && prmstr != "" ? transformToAssocArray(prmstr) : {};
    }
  
    function transformToAssocArray( prmstr ) {
      var params = {};
      var prmarr = prmstr.split("&");
      for ( var i = 0; i < prmarr.length; i++) {
          var tmparr = prmarr[i].split("=");
          params[tmparr[0]] = tmparr[1];
      }
      return params;
    }
  
    var params = getSearchParameters();

    const [editing, setEditing] = useState('')
    useEffect(() => {
      const storedCommands = JSON.parse(localStorage.getItem('COMMANDS'))
      if(storedCommands) setCommands(storedCommands)
      
      if(params.commands){
        setUsedCommands(params.commands.split(','))
      }

      if(params.workspace) setInput(JSON.parse(localStorage.getItem(params.workspace.toUpperCase())))
      console.log(params)
    }, [])

    useEffect(() => {
      localStorage.setItem("COMMANDS", JSON.stringify(commands))
      
    }, [commands])

    
      useEffect(() => {
        if(params.workspace)
        localStorage.setItem(params.workspace.toUpperCase(), JSON.stringify(input))
        
      }, [input])
    

  

  return (
    <>
    <ThemeProvider theme={outerTheme}>
      <AppBar position="fixed" color="primary" sx={{ top: 'auto', bottom: 0 }}>
        <Toolbar>
          <IconButton color="inherit" aria-label="open drawer">
            <MenuIcon />
          </IconButton>

          <Box sx={{ flexGrow: 1 }}/>
          
          <Add setTempCurrentEditing={setTempCurrentEditing} setEditing={setEditing} setCurrentEditing={setCurrentEditing}
          allCommands={allCommands} collection={collection} setCollection={setCollection} createNew={createNew} setCreateNew={setCreateNew} setIsEditing={setIsEditing}/>
          <Filter allCommands={allCommands} usedCommands={usedCommands} setUsedCommands={setUsedCommands}/>

        </Toolbar>
      </AppBar>
      
      <PlayGround setEditing={setEditing} editing={editing} tempCurrentEditing={tempCurrentEditing} currentEditing={currentEditing} setTempCurrentEditing={setTempCurrentEditing} setCurrentEditing={setCurrentEditing}
      theme={outerTheme} allCommands={commands} input={input} setInput={setInput} commands={commands.filter((a) => usedCommands.includes(a.name))} isEditing={isEditing} setCommands={setCommands} setIsEditing={setIsEditing }/>
      </ThemeProvider>
    </>
  );
}




