import Stack from '@mui/material/Stack';
import TextField from '@mui/material/TextField';
import Drawer from '@mui/material/Drawer';
import Dialog from '@mui/material/Dialog'
import CustomPrompt from './CustomPrompt.js'
import Button from '@mui/material/Button';
import Grid from '@mui/material/Grid';
import { FormControl, InputLabel, Select, MenuItem } from '@mui/material';
import FilterIcon from '@mui/icons-material/Filter';
import * as React from 'react';
import Menu from '@mui/material/Menu';
import {useState} from 'react';
import {useRef} from 'react'
export default function PlayGround({outerTheme, input, setInput, commands, editing, setEditing, allCommands, isEditing, setIsEditing, setCommands, tempCurrentEditing, setCurrentEditing, currentEditing, setTempCurrentEditing}){

    var inputElement = useRef(), outputElement = useRef();
    const [value, setValue] = React.useState('')
    const [output, setOutput] = useState('')
    const [title, setTitle] = useState('')
    const [open, setOpen] = useState(false)
    const [callback, setCallBack] = useState(() => () => {})
    

    const customPrompt = function(a, callback){
        setValue('')
        setCallBack(() => callback);
        setTitle(a)
        setOpen(true)
    }
    
    
    
    
    const [contextMenu, setContextMenu] = useState(null);

  const handleContextMenu = (event) => {
      setTempCurrentEditing(event.target.dataset.key);
    event.preventDefault();
    setContextMenu(
      contextMenu === null
        ? {
            mouseX: event.clientX - 2,
            mouseY: event.clientY - 4,
          }
        : null,
    );
  };

  const handleClose = () => {
    setIsEditing(false);
    setContextMenu(null);
  };

  const handleEdit = (event) => {
    setIsEditing(true);
    setContextMenu(null);
    setCurrentEditing(tempCurrentEditing.split('|')[1]);
    setEditing(tempCurrentEditing.split('|')[2])
  }

  const handleSave = () => {
    setContextMenu(null);
    var newCommands = JSON.parse(JSON.stringify(allCommands)), 
        newCommand = {name: tempCurrentEditing.split('|')[1], action: editing},
        newSet = newCommands.find(a => a.name === tempCurrentEditing.split('|')[0]) || {name: tempCurrentEditing.split('|')[0], actions: []}

    newSet.actions = [...(newSet.actions.filter(a => a.name !== tempCurrentEditing.split('|')[1])), newCommand]
    newCommands = [...(newCommands.filter(a => a.name !== tempCurrentEditing.split('|')[0])), newSet]
    setCommands(newCommands)
  }

  const handleDelete = () => {
    setIsEditing(false);
    setContextMenu(null);
    var newCommands = JSON.parse(JSON.stringify(allCommands)), 
        newSet = newCommands.find(a => a.name === tempCurrentEditing.split('|')[0])

    newSet.actions = [...(newSet.actions.filter(a => a.name !== tempCurrentEditing.split('|')[1]))]
    newCommands = [...(newCommands.filter(a => a.name !== tempCurrentEditing.split('|')[0])), newSet]
    setCommands(newCommands)
  }

    var mobile = document.documentElement.clientWidth < 900, height = document.documentElement.clientHeight, rows = 0.0225*height - 7.27

    var commandsList = [];

    commands.forEach((a) =>ã€€commandsList.push(...(a.actions.map(b => ({...b, 'parent': a.name})))))



    return (
    <>
        <Grid container spacing={4} style={{'padding': '0px 32px'}} sx={{mt: 1, mb: 10}}>
            
            <Grid item xs={12} md={isEditing? 2 : 6} ref={inputElement}>
                <TextField id="outlined-basic" label="Input" variant="outlined" multiline rows={mobile ? rows: 30} style={{'width': '100%'}} theme={outerTheme} value={input} onChange={(event) => setInput(event.target.value)}/>
            </Grid>
            <Grid item xs={12} md={isEditing? 4 : 6} ref={outputElement}>
                <TextField id="outlined-basic" label="Output" variant="outlined" multiline rows={mobile ? rows : 30} style={{'width': '100%'}} theme={outerTheme} value={output} onChange={(event) => setOutput(event.target.value)}/>
            </Grid>{isEditing &&
            <Grid item xs={12} md={isEditing? 6 : 6}>
                <TextField id="outlined-basic" spellCheck={false} label={currentEditing} variant="outlined" multiline rows={30} style={{'width': '100%', 'font-family': 'Roboto Mono!important'}} theme={outerTheme} value={editing} onChange={(event) => setEditing(event.target.value)}/>
            </Grid>}{isEditing &&
            <Grid item md={6} xs={12}>
                <Stack direction="row" spacing={1}>
                    <Button variant="contained" color="primary" onClick={handleSave}>Save</Button>
                    <Button variant="contained" color="primary" onClick={handleClose}>Close</Button>
                </Stack>
            </Grid>}
            <Grid item md={6} xs={12}>
                {
                    commandsList.map((a) => (
                        <Button sx={{mr: 1, mb: 1}} variant="contained" color="secondary" onClick={() => eval(isEditing && a.name ===  tempCurrentEditing.split('|')[1] ? editing: a.action)} data-key={`${a.parent}|${a.name}|${a.action}`}  onContextMenu={handleContextMenu} >{a.name}</Button>
                    ))}
                <Menu
                    open={contextMenu !== null}
                    onClose={handleClose}
                    anchorReference="anchorPosition"
                    anchorPosition={
                    contextMenu !== null
                        ? { top: contextMenu.mouseY, left: contextMenu.mouseX }
                        : undefined
                    }
                >
                    <MenuItem onClick={handleEdit}>Edit</MenuItem>
                    <MenuItem onClick={handleDelete}>Delete</MenuItem>
                </Menu>
            </Grid>
        </Grid>
        <CustomPrompt value={value} setValue={setValue} title={title} setOpen={setOpen} open={open} callback={callback} output={output} input={input} setOutput={setOutput} setInput={setInput} />
        </>
    )
}